
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex'
import SinceCompent from '../order/sinceCompent.vue';
import {API,getQuery} from '../../services';

export default {
components: {
XHeader,
Group,
Checker,
CheckerItem,
SinceCompent,
Toast
},
data () {
return {
confrim:"请选择物流方式",
toast:false,
val:"",
tplobj:{},
sinceList:[],//获取自提点列表
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
])
},
methods:{
/* 路由回退 */
routerBack(){
this.$store.dispatch("CurrentTpl",this.val);
this.$router.goBack();
},
/* 数据初始化 */
dataForm(){
/* 获取物流方式 */
API.order.orderTpl({
province:this.$store.state.addressData.province,
city:this.$store.state.addressData.city,
region:this.$store.state.addressData.district,
goodsId:this.$route.params.id
}).then((res)=>{
if(res.body.code==200){
let tpl={};
for(let item of res.body.data.tpl) {
tpl[item.goods_shipping_tpl_type]=1;
}
this.tplobj=tpl;
if(tpl[3]){
/* 获取自提点列表 */
API.order.goodsSince({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
province:this.$store.state.addressData.province,
city:this.$store.state.addressData.city,
region:this.$store.state.addressData.district,
goodsId:this.$route.params.id
}).then((res)=>{
/*  */
if(res.body.code==200){
this.sinceList=res.body.data.ztd.data;
}
});
}
}
});

},
/* 触发状态管理 */
stateChange(){
if(this.val==""){
this.toast=true;
return false;

}else if(this.val=="3"){
this.confrim="请选择自提点";
this.toast=true;
return false;
}
else{
this.$store.dispatch("CurrentTpl",this.val);
this.$router.goBack();
}
},

},
mounted(){

},
activated(){
this.dataForm();
}
}



